nav {
   background: #1c202a;
   &.fixed__nav {
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      z-index: 9999;
      background: #1c202a;
      .nav {
         padding: 18px 0;
         transition: all 0.3s ease;
      }
   }
}
.nav {
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 23px 0;
   transition: all 0.3s ease;
   .nav__logo{
      @media (max-width: 480px){
         z-index: 100;
         width: 159.44px;
         img{
            width: 100%;
         }
      }
   }
   .burger {
      display: none;
      @media (max-width: 480px){
         display: block;
         z-index: 100;
      }
   }
   &__registr {
      display: flex;
      align-items: center;
      gap: 10px;
      @media (max-width: 480px){
         display: none;
         &.active{
            display: flex;
            justify-content: center;
            flex-direction: column;
            position: fixed;
            gap: 15px;
            right: 0;
            left: 0;
            top: 0;
            bottom: 0;
            background: #1c202a;
            z-index: 99;
            animation: fade-in .2s ease;
         }
      }
   }
   &__link {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 400;
      font-size: 21px;
      line-height: 150%;
      color: #ffffff;
      position: relative;
      @media (max-width: 480px){
         font-size: 28px;
      }
      &.break{
         cursor: default;
         @media (max-width: 480px){
            display: none;
         }
         &::after {
            display: none;
         }
      }
      &::after {
         content: "";
         position: absolute;
         bottom: 0;
         left: 0;
         width: 100%;
         height: 2px;
         background: #ffffff;
         transform: translate(-50%, 0) scaleX(0);
         transition: all 0.3s ease;
      }
      &:hover::after {
         transform: translate(0, 0) scaleX(1);
         transition: all 0.3s ease;
      }
   }
}
