.section {
   &__best {
      padding: 60px 0;
      background: #e29e30;
   }
}
.best {
   &__header {
      margin-bottom: 48px;
      text-align: center;
      @media (max-width: 480px){
         margin-bottom: 28px;
      }
   }
   &__title {
      font-family: "Futura PT Bold";
      font-size: 53px;
      line-height: 106.7%;
      color: #000000;
      margin-bottom: 11px;
      @media (max-width: 480px){
         font-size: 24px;
      }
   }
   &__text {
      font-family: "Futura PT Book";
      font-weight: 200;
      font-size: 21px;
      line-height: 27px;
      color: #000000;
      @media (max-width: 480px){
         font-size: 16px;
      }
   }
   &__items {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 20px;
      margin-bottom: 90px;
      @media (max-width: 768px){
         flex-wrap: wrap;
      }
      @media (max-width: 480px){
         margin-bottom: 38px;
      }
   }
   &__item {
      width: 195.79px;
      height: 195.79px;
      background: #ffffff;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      border-radius: 50%;
      padding: 20px;
      .best__text{
         @media (max-width: 480px){
            font-size: 21px;
         }
      }
   }
   &__item-text {
      font-family: "Futura PT Book";
      font-weight: 200;
      font-size: 18px;
      line-height: 23px;
      color: #000000;
   }
   &__button {
      display: flex;
      justify-content: center;
   }
   &__btn {
      letter-spacing: 0.1em;
      background: #ffffff;
      border-radius: 5px;
      padding: 14px 27px;
      @media (max-width: 480px){
         width: 100%;
         padding: 14px 0;
         font-size: 16px;
         text-align: center;
      }
   }
}
