.section {
   &__support {
      padding: 60px 0 32px;
   }
}
.support {
   &__title {
      font-family: "Futura PT Bold";
      font-size: 53px;
      line-height: 106.7%;
      text-align: center;
      color: #000000;
      margin-bottom: 30px;
      @media (max-width: 480px){
         font-size: 24px;
      }

   }
   &__item {
      background: #1c202a;
      border-radius: 5px;
      position: relative;
      overflow: hidden;
      max-width: 722px;
      width: 100%;
      margin: 0 auto;
      padding: 55px 10px 55px 100px;
      margin-bottom: 50px;
      @media (max-width: 768px){
         padding: 55px 10px 55px 70px;
      }
      @media (max-width: 500px){
         padding: 55px 10px 55px 40px;
      }
   }
   &__list {
      li::marker {
         color: #e29d30;
      }
   }
   &__list-item {
      font-family: "Futura PT Bold";
      font-size: 21px;
      line-height: 1.26;
      color: #ffffff;
      @media (max-width: 400px){
         font-size: 16px;
      }
   }
   &__soon {
      font-family: "Futura PT Bold";
      font-size: 34px;
      line-height: 44px;
      color: #ffffff;
      width: 175.33px;
      height: 175.33px;
      background: #e29d30;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      right: 10px;
      top: -20px;
      @media (max-width: 480px){
         width: 104.5px;
         height: 104.5px;
         font-size: 18px;
         line-height: 1.5;
         top: auto;
         bottom: -5px;
         right: -5px;
      }
   }
   &__info {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 33px;
      @media (max-width: 480px){
         flex-direction: column;
         gap: 28px;
      }
   }
   &__info-img {
   }
   &__text {
      font-family: "Futura PT Book";
      font-weight: 200;
      font-size: 18px;
      line-height: 23px;
      color: #000000;
      max-width: 485.8px;
      width: 100%;
      @media (max-width: 400px){
         font-size: 16px;
      }
   }
}
