.section {
   &__hero {
      background: url(..//img/hero-bg.png) center no-repeat;
      background-size: cover;
      padding: 144px 0 249px;
      overflow: hidden;
      position: relative;
      @media (max-width: 768px){
         padding: 110px 0;
         &::after{
            content: '';
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            background: #FFFFFF;
            opacity: 0.7;
         }
      }
      @media (max-width: 480px){
         padding: 61px 0;
      }
   }
}
.hero {
   @media (max-width: 768px){
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: center;
      gap: 57px;
   }
   &__item {
      position: relative;
      &:first-child{
         max-width: 503.19px;
         width: 100%;
         margin-left: 100px;
         z-index: 2;
         @media (max-width: 992px){
            margin-left: 0;
         }
         @media (max-width: 768px){
            margin: 0 auto;   
         }
         &::after{
            content: '';
            position: absolute;
            left: -234px;
            top: -80%;
            transform: translate(0, -50%);
            background: #FFFFFF;
            opacity: 0.3;
            border: 2px solid #333333;
            width: 858.2px;
            height: 858.2px;
            border-radius: 50%;
            z-index: -1;
            @media (max-width: 1122px){
               left: -260px;
            }  
            @media (max-width: 768px){
               display: none;
            }
         }
      }
      &:nth-child(2){
         position: relative;
         &::after{
            content: '';
            position: absolute;
            width: 685.24px;
            height: 685.24px;
            right: -100px;
            top: -35px;
            background: #1C202A;
            opacity: 0.5;
            border-radius: 50%;
            @media (max-width: 768px){
               display: none;
            }
         }
      }
   }
   &__title {
      font-family: "Futura PT Book";
      font-size: 28px;
      line-height: 1.5;
      color: #000000;
      font-weight: 200;
      @media (max-width: 480px){
         font-size: 21px;
      }
   }
   &__btn {
      font-family: "Futura PT Bold";
      font-style: normal;
      font-weight: 700;
      font-size: 21px;
      line-height: 1.5;
      text-align: center;
      letter-spacing: 0.1em;
      color: #ffffff;
      padding: 14px 27px;
      background: #E29E30;
      border-radius: 5px;
      position: absolute;
      right: 90px;
      bottom: -100px;
      z-index: 2;
      @media (max-width: 768px){
         position: relative;
         right: 0;
         bottom: 0;
      }
      @media (max-width: 480px){
         font-size: 16px;
         width: 100%;
         padding: 14px 0;
         display: flex;
         justify-content: center;
         align-items: center;
      }
   }
}
