.section {
   &__info {
      padding: 60px 0;
   }
}
.info {
   display: flex;
   justify-content: center;
   align-items: center;
   gap: 20px;
   @media (max-width: 1122px){
      flex-wrap: wrap;
   }
   &__item {
      background: #1c202a;
      border-radius: 5px;
      padding: 26px 24px 26px 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 5px;
      height: 144px;
      width: 100%;
      flex: 0 1 25%;
      @media (max-width: 1122px){
         flex: 0 1 45%;
      }
      @media (max-width: 550px){
         flex: 0 1 100%;
      }
   }
   &__img {
   }
   &__text {
      font-family: "Futura PT Book";
      font-weight: 200;
      font-size: 18px;
      line-height: 23px;
      text-align: right;
      text-transform: uppercase;
      color: #ffffff;
      @media (max-width: 480px){
         font-size: 16px;
      }
      @media (max-width: 350px){
         width: 133.76px;
      }
   }
}
