.section {
   &__learn {
      padding: 60px 0;
      background: #e29e30;
      position: relative;
   }
}
.learn {
   &__test {
      background: url(..//img/test-bg.svg) left top no-repeat, #1c202a;
      background-size: contain;
      padding: 46px 69px 46px 114px;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 84px;
      margin-bottom: 40px;
      @media (max-width: 1122px){
         padding: 46px 69px;
      }
      @media (max-width: 992px){
         flex-direction: column;
         gap: 25px;
         padding: 30px 20px 60px;
         text-align: center;
      }
      @media (max-width: 480px){
         padding: 30px 10px;
      }
   }
   &__test-text {
      font-family: "Futura PT Bold";
      font-size: 21px;
      line-height: 1.27;
      color: #ffffff;
      &:first-child {
         max-width: 402.98px;
         width: 100%;
      }
   }
   &__btn {
      font-family: "Futura PT Bold";
      font-size: 21px;
      line-height: 1.27;
      color: #000000;
      letter-spacing: 0.1em;
      padding: 14px 27px;
      background: #ffffff;
      border-radius: 5px;
      @media (max-width: 400px){
         font-size: 16px;
         width: 100%;
         padding: 14px 0;
         text-align: center;
      }
   }
   &__title {
      font-family: "Futura PT Bold";
      font-size: 53px;
      line-height: 106.7%;
      text-align: center;
      color: #ffffff;
      @media (max-width: 480px) {
         font-size: 24px;
      }
   }
   &__text {
      font-family: "Futura PT Book";
      font-weight: 200;
      font-size: 21px;
      line-height: 1.27;
      color: #000000;
      @media (max-width: 480px){
         font-size: 16px;
      }
      &:nth-child(2) {
         text-align: center;
         margin: 16px 0 24px;
      }
   }
   &__items {
      display: flex;
      gap: 24px;
      align-items: center;
      justify-content: center;
      @media (max-width: 768px) {
         flex-direction: column;
         max-width: 400px;
         margin: 0 auto;
      }
      @media (max-width: 480px){
         gap: 9px;
         max-width: 100%;
      }
   }
   &__item {
      display: flex;
      align-items: center;
      gap: 31px;
      @media (max-width: 768px){
         flex: 0 1 50%;
         width: 100%;
         .learn__title{
            max-width: 90px;
            width: 100%;
            text-align: left !important;
         }
      }
      @media (max-width: 480px){
         gap: 12px;
         padding: 0 30px;
         .learn__title{
            max-width: 40px;
         }
      }
      @media (max-width: 320px){
         padding: 0 20px;
      }
      &:first-child {
         .learn__text {
            text-align: left !important;
            max-width: 253px;
            width: 100%;
            margin: 0 !important;
         }
      }
      &:nth-child(2) {
         .learn__text {
            max-width: 130px;
            width: 100%;
            text-align: left !important;
            margin: 0 !important;
         }
      }
   }
   &__swiper {
      padding: 40px 0 345px;
      @media (max-width: 500px) {
         padding: 40px 0 280px;
      }
      @media (max-width: 350px) {
         padding: 40px 0 310px;
      }
      .swiper {
         position: absolute;
         left: 0;
         right: 0;
         width: 100%;
         @media (min-width: 1600px) {
            max-width: 1600px;
         }
      }
   }
   .swiper-slide {
      opacity: 0.7;
      transition: all 0.3s ease;
      height: 100%;
      &-active {
         opacity: 1;
         transition: all 0.3s ease;
      }
   }
   .swiper__buttons {
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;
      @media (max-width: 1280px) {
         max-width: 680px;
         margin: 0 auto;
      }
      @media (max-width: 768px) {
         display: none;
      }
   }
   .swiper-pagination {
      display: none;
      bottom: 150px;
      @media (max-width: 768px) {
         display: flex;
         justify-content: center;
         align-items: center;
      }
      &-bullet {
         width: 8.82px;
         height: 8.82px;
         background: #ffffff;
         opacity: 1;
         transition: all 0.3s ease;
         z-index: 99;
         &-active {
            background: #2b2b2b;
            transition: all 0.3s ease;
         }
      }
   }
   .swiper-next {
      right: 0;
      img {
         transform: rotate(-180deg);
         margin-left: 5px;
      }
   }
   .swiper__btn {
      width: 68.47px;
      height: 68.47px;
      background: #ffffff;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: 100px;
      z-index: 99;
      cursor: pointer;
      img {
         margin-right: 5px;
      }
   }
   .slide {
      padding: 70px 89px 100px 147px;
      background: url(..//img/slide-bg.svg) right top no-repeat, #1c202a;
      border-radius: 5px;
      position: relative;
      height: 285px;
      @media (max-width: 1280px) {
         padding: 50px 40px 50px 100px;
      }
      @media (max-width: 500px) {
         padding: 25px 33px 25px 26px;
         height: 200px;
      }
      @media (max-width: 350px) {
         height: 240px;
      }
      &__title {
         font-family: "Futura PT Bold";
         font-size: 34px;
         line-height: 1.27;
         color: #e29e30;
         margin-bottom: 18px;
         @media (max-width: 500px) {
            font-size: 21px;
         }
      }
      &__text {
         color: #ffffff;
      }
      &__num {
         font-family: "Futura PT Bold";
         font-style: normal;
         font-weight: 700;
         font-size: 143.297px;
         line-height: 184px;
         color: #ffffff;
         opacity: 0.1;
         position: absolute;
         top: 0;
         left: 45px;
         @media (max-width: 1280px) {
            left: 10px;
         }
      }
   }
   &__button {
      display: flex;
      justify-content: center;
   }
}
