.section {
   &__feature {
      padding: 40px 0;
   }
}
.feature {
   display: flex;
   align-items: center;
   justify-content: center;
   gap: 75px;
   @media (max-width: 992px){
      gap: 10px;
      flex-wrap: wrap;
   }  
   &__item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 10px;
      background: #1C202A;
      border-radius: 100px;
      width: 187.37px;
      height: 187.37px;
      @media (max-width: 992px){
         width: 145px;
         height: 145px;
         padding: 15px;
      }
   }
   &__text {
      font-family: "Futura PT Book";
      font-style: normal;
      font-weight: 200;
      font-size: 18px;
      line-height: 1.27;
      text-align: center;
      color: #ffffff;
      @media (max-width: 992px){
         font-size: 15px;
      }
      
   }
}
